html,
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; 
  /*background-color: rgb(245, 245, 245);*/
  background-color: #9fa6f5;
}
.container {
  /*text-align: center;*/
  margin: 80px auto 0 auto;
  /*margin-left: 100px;*/
  max-width: 1200px;
  max-height: 1000px;
}

.nav-container {
  margin: auto;
  
}
.nav-container .button {
  color: #fff;
}

a {
  text-decoration: none
}
.paper{
  padding: 20px;
}
/* from tinder card */
#root {
  text-align: left;
  display: flex;
  justify-content: center;
  width: 100vw;
  /*min-height: 100vh;
  overflow: hidden;*/
  /* background: linear-gradient(#fff, #999); */
  background: repeating-linear-gradient(#e66465, #9fa6f5);
  background-color: #9198e5;
  
}

#root>div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
